@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

:root {
  --primary-dark: #F59E0B;
  --primary-light: #5651ef ;
}

.icon {
  color: var(--primary-dark) ;
}

@layer base {
  body {
    @apply font-[Poppins]
  }

  li {
    @apply p-4
  }

  h1 {
    @apply text-3xl md:text-4xl font-bold
  }

  h2 {
    @apply text-2xl
  }

  h3 {
    @apply text-xl font-bold
  }

  button {
    @apply p-3 border bg-amber-500 text-black font-extrabold rounded-md
  }
.icon {
  @apply text-2xl cursor-pointer
}
}

.embla {
  overflow: hidden;
}
.embla__container {
  display: flex;
}
.embla__slide {
  flex: 0 0 100%;
  min-width: 0;
}
.embla__slide {
  width: 100%;
}